<template>
  <div id="app">
    <img src="./assets/001.png">
    <div class="addUs" @click="addUs">扫描下方二维码加入</div>
    <img class="qrCode"  src="./assets/qrcode.jpeg" alt="">
  </div>
  
</template>

<script>
export default {
  name: 'App',
    data: {
    showQrCode: false
  },
  methods:{
    addUs(){
     this.data.showQrCode = true 
    }
  }
}
</script>

<style>
#app {
  color: #2c3e50;
  display: flex;
  flex-direction: column;
  color: white;
}
img {
  width: 100%;
}
.addUs{
  background-color: black;
  color: white;
  text-align: center;
  width: 80%;
  height: 40px;
  margin-left: 10%;
  margin-top: 10%;
  text-align: center;
  line-height: 40px;
}
.qrCode{
  width: 100%;
  margin-top: 80px;
}
</style>



